.achievements-page {
    padding: 20px;
    background-color: #f4f4f4;
    color: #333;
  }
  
  .title {
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  .achievements-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .achievement-card {
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    width: 300px;
    text-align: center;
    padding: 20px;
  }
  
  .achievement-image {
    width: 100%;
    height: auto;
    max-width: 250px; /* Adjust the max width as needed */
    max-height: 150px; /* Adjust the max height as needed */
    object-fit: cover; /* Ensure the image covers the specified dimensions without distortion */
    margin: 0 auto; /* Center the image within the card */
    display: block; /* Ensure the image is treated as a block element */
  }
  
  .achievement-content {
    padding: 10px 0;
  }
  
  .achievement-title {
    font-size: 1.5rem;
    margin: 15px 0;
  }
  
  .achievement-description {
    font-size: 1rem;
    color: #666;
  }
  
  .achievement-date {
    font-size: 0.9rem;
    color: #999;
  }
  